import React from 'react';
import { useNavigate } from 'react-router-dom';
import { FiTrendingUp, FiAward, FiArrowRight } from 'react-icons/fi';

const TopMigrationThreshold = ({ isDarkMode }) => {
  const navigate = useNavigate();
  
  const recentlyMigratedTokens = [
    { name: "CurveCoin", symbol: "CRV", marketCap: "$3,210,987", threshold: "40.0 ETH", tokenId: "crv" },
    { name: "BaseToken", symbol: "BASE", marketCap: "$2,456,789", threshold: "40.0 ETH", tokenId: "base" },
    { name: "DexFi", symbol: "DEXF", marketCap: "$1,234,567", threshold: "40.0 ETH", tokenId: "dexf" },
    { name: "LiquidFi", symbol: "LIQF", marketCap: "$876,543", threshold: "40.0 ETH", tokenId: "liqf" }
  ];

  const handleTokenClick = (tokenId) => {
    navigate(`/swap?tokenId=${tokenId}`);
  };

  const handleViewMore = () => {
            navigate('/track?tab=recently-migrated');
  };

  return (
    <div className="form-card">
      <div className="section-header">
        <h3 className="section-title">
          <FiAward />
          Recently Migrated Tokens
        </h3>
        <button className="view-more-btn" onClick={handleViewMore}>
          View More <FiArrowRight />
        </button>
      </div>
      <div className="threshold-list">
        {recentlyMigratedTokens.map((token, index) => (
          <div 
            key={index} 
            className="threshold-item"
            onClick={() => handleTokenClick(token.tokenId)}
            style={{ cursor: 'pointer' }}
          >
            <div className="token-rank">
              <span className="rank-number">{index + 1}</span>
              <div className="token-details">
                <h4 className="token-name">{token.name}</h4>
                <span className="token-symbol">{token.symbol}</span>
              </div>
            </div>
            <div className="threshold-value">
              {token.threshold}
            </div>
          </div>
        ))}
      </div>
    </div>
  );
};

export default TopMigrationThreshold; 