import React from 'react';
import { 
  FiHome, 
  FiPlus, 
  FiSettings, 
  FiCreditCard, 
  FiTrendingUp 
} from 'react-icons/fi';

const FooterNav = ({ currentPage, onNavigate }) => {
  const navItems = [
    { id: 'home', label: 'Home', icon: FiHome },
    { id: 'create-token', label: 'Create Token', icon: FiPlus },
    { id: 'manage-token', label: 'Manage Token', icon: FiSettings },
    { id: 'wallet', label: 'My Wallet', icon: FiCreditCard },
    { id: 'trending', label: 'Track', icon: FiTrendingUp }
  ];

  const handleNavClick = (itemId) => {
    onNavigate(itemId);
  };

  return (
    <footer className="footer">
      <div className="footer-nav">
        {navItems.map((item) => {
          const isActive = currentPage === item.id;
          return (
            <div
              key={item.id}
              onClick={() => handleNavClick(item.id)}
              className={`nav-item ${isActive ? 'active' : ''}`}
            >
              <item.icon />
              <span>{item.label}</span>
            </div>
          );
        })}
      </div>
    </footer>
  );
};

export default FooterNav; 