import React, { useState } from 'react';
import StatsOverview from './StatsOverview';
import TopMigrationThreshold from './TopMigrationThreshold';
import TrendingTokens from './TrendingTokens';
import TokenCards from './TokenCards';

const Dashboard = ({ 
  isDarkMode, 
  likedTokens, 
  expandedDescriptions, 
  toggleLike, 
  toggleDescription, 
  copyAddress 
}) => {
  const [activeFilter, setActiveFilter] = useState('All');

  const filterOptions = [
    { id: 'All', label: 'All Tokens' },
    { id: 'Migrated', label: 'Migrated' },
    { id: 'Migrating', label: 'Migrating' },
    { id: 'New', label: 'New' }
  ];

  return (
    <div className="dashboard-container">
      {/* Stats Overview */}
      <StatsOverview isDarkMode={isDarkMode} />

      {/* Top Migration Threshold */}
      <TopMigrationThreshold isDarkMode={isDarkMode} />

      {/* Trending Tokens */}
      <TrendingTokens isDarkMode={isDarkMode} />

      {/* Token Cards Section */}
      <div className="token-section">
        {/* Filter Tabs */}
        <div className="filter-container">
          {filterOptions.map((option) => (
            <button
              key={option.id}
              onClick={() => setActiveFilter(option.id)}
              className={`filter-btn ${activeFilter === option.id ? 'active' : ''}`}
            >
              {option.label}
            </button>
          ))}
        </div>

        {/* Token Cards */}
        <TokenCards 
          activeFilter={activeFilter}
          isDarkMode={isDarkMode}
          likedTokens={likedTokens}
          expandedDescriptions={expandedDescriptions}
          toggleLike={toggleLike}
          toggleDescription={toggleDescription}
          copyAddress={copyAddress}
        />
      </div>
    </div>
  );
};

export default Dashboard; 