import React, { useState } from 'react';
import {
  FiSearch,
  FiMoon,
  FiSun,
  FiPlus,
  FiCreditCard,
  FiX
} from 'react-icons/fi';
import './Header.css';

const Header = ({ isDarkMode, onToggleTheme, currentPage, onNavigate }) => {
  const [searchQuery, setSearchQuery] = useState('');
  const [isWalletConnected, setIsWalletConnected] = useState(true);

  const handleSearch = (e) => {
    e.preventDefault();
    // Handle search functionality
    console.log('Searching for:', searchQuery);
  };

  const handleCreateToken = () => {
    onNavigate('create-token');
  };

  const handleWalletClick = () => {
    if (isWalletConnected) {
      // If connected, navigate to wallet page
      onNavigate('wallet');
    } else {
      // If disconnected, connect wallet
      setIsWalletConnected(true);
    }
  };

  const handleLogoClick = () => {
    onNavigate('home');
  };

  return (
    <header className="header">
      {/* Left Side - Logo */}
      <div className="logo" onClick={handleLogoClick}>
        <div className="logo-icon">
          <svg xmlns="http://www.w3.org/2000/svg" className="h-5 w-5 text-white" viewBox="0 0 20 20" fill="currentColor">
            <path d="M13 10V3L4 14h7v7l9-11h-7z" />
          </svg>
        </div>
        <span>RAVO</span>
      </div>

      {/* Center - Search Bar */}
      <div className="search-container">
        <FiSearch className="search-icon" />
        <input
          type="text"
          placeholder="Search tokens, contracts, or creators..."
          value={searchQuery}
          onChange={(e) => setSearchQuery(e.target.value)}
          className="search-input"
        />
        {searchQuery && (
          <button
            type="button"
            onClick={() => setSearchQuery('')}
            className="clear-search-btn"
          >
            <FiX size={16} />
          </button>
        )}
      </div>

      {/* Right Side - Actions */}
      <div className="header-actions">
        {/* Theme Toggle */}
        <button
          onClick={onToggleTheme}
          className="theme-toggle"
          title={isDarkMode ? 'Switch to Light Mode' : 'Switch to Dark Mode'}
        >
          {isDarkMode ? <FiSun /> : <FiMoon />}
        </button>

        {/* Create Token Button */}
        <button
          onClick={handleCreateToken}
          className={`btn btn-primary ${currentPage === 'create-token' ? 'active' : ''}`}
        >
          <FiPlus className="inline mr-2" />
          <span className="btn-text">Create Token</span>
        </button>

        {/* Wallet Button */}
        <button
          onClick={handleWalletClick}
          className={`btn ${isWalletConnected ? 'btn-primary' : 'btn-secondary'}`}
          title={isWalletConnected ? 'View Wallet' : 'Connect Wallet'}
        >
          <FiCreditCard />
        </button>
      </div>
    </header>
  );
};

export default Header; 