import React, { useState } from 'react';
import { useNavigate } from 'react-router-dom';
import { 
  FiHeart, 
  FiShare2, 
  FiExternalLink, 
  FiMessageCircle,
  FiChevronDown,
  FiChevronUp,
  FiCopy
} from 'react-icons/fi';

const TokenCards = ({ 
  activeFilter, 
  isDarkMode
}) => {
  const navigate = useNavigate();
  
  // Local state for likes and expanded descriptions
  const [likedTokens, setLikedTokens] = useState({
    1: 24,
    2: 42,
    3: 18,
    4: 56
  });
  const [expandedDescriptions, setExpandedDescriptions] = useState({});
  const [copiedAddress, setCopiedAddress] = useState(null);

  const tokens = [
    {
      id: 1,
      name: "BaseToken",
      symbol: "BASE",
      logo: "https://ipfs.io/ipfs/QmS9fN6tXU9bYh7h4cX3JxXvV7Lg2bR8bX8XkY7XfX6Xk",
      migrationStatus: "Migrated",
      description: "The official token of the Base ecosystem. Designed for long-term growth and community-driven development on the Base blockchain. This innovative token features a unique bonding curve mechanism that ensures fair distribution and sustainable growth.",
      marketCap: "$2,456,789",
      ethInPool: "42.5 ($153,000)",
      bondingProgress: 100,
      migrationThreshold: "40.0",
      tradingOpen: true,
      launchTime: 0,
      socials: {
        telegram: "https://t.me/basetoken",
        website: "https://basetoken.io",
        twitter: "https://twitter.com/basetoken"
      },
      address: '0x742d35Cc6634C0532925a3b8D4C9db96C4b4d8b6'
    },
    {
      id: 2,
      name: "DexFi",
      symbol: "DEXF",
      logo: null,
      migrationStatus: "Migrating",
      description: "Revolutionizing decentralized finance with automated bonding curves and seamless migration to Uniswap. The future of token launches. DexFi provides a comprehensive suite of DeFi tools that make token creation and management accessible to everyone.",
      marketCap: "$1,234,567",
      ethInPool: "25.78 ($92,800)",
      bondingProgress: 68,
      migrationThreshold: "40.0",
      tradingOpen: true,
      launchTime: 0,
      socials: {
        telegram: "https://t.me/dexfi",
        website: "https://dexfi.io",
        twitter: "https://twitter.com/dexfi"
      },
      address: '0x8ba1f109551bD432803012645Hac136c772c3742'
    },
    {
      id: 3,
      name: "CurveCoin",
      symbol: "CRV",
      logo: null,
      migrationStatus: "Migrating",
      description: "Innovative token with a unique bonding curve mechanism. Designed for sustainable growth and fair distribution to early supporters. CurveCoin's algorithm adjusts token economics in real-time based on market conditions, ensuring optimal liquidity and price stability through all market cycles.",
      marketCap: "$3,210,987",
      ethInPool: "58.2 ($209,500)",
      bondingProgress: 84,
      migrationThreshold: "40.0",
      tradingOpen: true,
      launchTime: 0,
      socials: {
        telegram: "https://t.me/curvecoin",
        website: "https://curvecoin.io",
        twitter: "https://twitter.com/curvecoin"
      },
      address: '0x1234567890abcdef1234567890abcdef12345678'
    },
    {
      id: 4,
      name: "LiquidFi",
      symbol: "LIQF",
      logo: "https://ipfs.io/ipfs/QmS9fN6tXU9bYh7h4cX3JxXvV7Lg2bR8bX8XkY7XfX6Xk",
      migrationStatus: "New",
      description: "The next generation of liquid staking derivatives. Earn rewards while maintaining liquidity through our innovative bonding curve model. LiquidFi solves the liquidity problem in traditional staking by providing instant access to staked assets through a sophisticated tokenomics model.",
      marketCap: "$876,543",
      ethInPool: "18.9 ($68,040)",
      bondingProgress: 52,
      migrationThreshold: "40.0",
      tradingOpen: false,
      launchTime: Date.now() + 2 * 60 * 60 * 1000, // 2 hours from now
      socials: {
        telegram: "https://t.me/liquidfi",
        website: "https://liquidfi.io",
        twitter: "https://twitter.com/liquidfi"
      },
      address: '0xabcdef1234567890abcdef1234567890abcdef12'
    }
  ];

  // Filter tokens based on activeFilter
  const filteredTokens = tokens.filter(token => {
    if (activeFilter === 'all') return true;
    if (activeFilter === 'migrated') return token.migrationStatus === 'Migrated';
    if (activeFilter === 'migrating') return token.migrationStatus === 'Migrating';
    if (activeFilter === 'new') return token.migrationStatus === 'New';
    return true;
  });

  const getCountdown = (token) => {
    if (token.tradingOpen) {
      return 'Trading Open';
    }
    
    const now = Date.now();
    const timeLeft = token.launchTime - now;
    
    if (timeLeft <= 0) {
      return 'Launching...';
    }
    
    const hours = Math.floor(timeLeft / (1000 * 60 * 60));
    const minutes = Math.floor((timeLeft % (1000 * 60 * 60)) / (1000 * 60));
    
    return `${hours}h ${minutes}m`;
  };

  const formatAddress = (address) => {
    return `${address.slice(0, 6)}...${address.slice(-4)}`;
  };

  const handleCardClick = (tokenId) => {
    navigate(`/swap?token=${tokenId}`);
  };

  const toggleLike = (tokenId) => {
    setLikedTokens(prev => ({
      ...prev,
      [tokenId]: prev[tokenId] + 1
    }));
  };

  const toggleDescription = (tokenId) => {
    setExpandedDescriptions(prev => ({
      ...prev,
      [tokenId]: !prev[tokenId]
    }));
  };

  const copyAddress = (address) => {
    navigator.clipboard.writeText(address);
    setCopiedAddress(address);
    setTimeout(() => setCopiedAddress(null), 2000);
  };

  return (
    <div className="space-y-4">
      {filteredTokens.map(token => (
        <div 
          key={token.id}
          className={`rounded-xl overflow-hidden shadow-md transform transition-all duration-300 hover:scale-[1.005] hover:shadow-lg cursor-pointer ${
            isDarkMode ? 'bg-gray-800/50 border border-gray-700' : 'bg-white/50 border border-gray-200 backdrop-blur-sm'
          }`}
          onClick={() => handleCardClick(token.id)}
        >
          <div className="flex flex-col md:flex-row">
            {/* Left Section - Logo and Basic Info */}
            <div className={`p-4 flex-shrink-0 ${isDarkMode ? 'bg-gray-700/30' : 'bg-gray-50'}`}>
              <div className="flex items-center">
                <div className="w-16 h-16 rounded-xl overflow-hidden flex-shrink-0 flex items-center justify-center bg-gradient-to-br from-blue-600 to-purple-600 text-white">
                  {token.logo ? (
                    <img src={token.logo} alt={`${token.name} logo`} className="w-full h-full object-cover" />
                  ) : (
                    <span className="text-2xl font-bold">{token.name.charAt(0)}</span>
                  )}
                </div>
                <div className="ml-4">
                  <h3 className="text-xl font-bold">{token.name}</h3>
                  <p className={`text-sm ${isDarkMode ? 'text-gray-400' : 'text-gray-500'}`}>{token.symbol}</p>
                  <span className={`inline-block mt-2 px-2 py-1 rounded-full text-xs font-medium ${
                    token.migrationStatus === 'Migrated' 
                      ? 'bg-green-100 text-green-800' 
                      : token.migrationStatus === 'Migrating'
                        ? 'bg-blue-100 text-blue-800'
                        : 'bg-yellow-100 text-yellow-800'
                  }`}>
                    {token.migrationStatus}
                  </span>
                </div>
              </div>
            </div>
            
            {/* Middle Section - Stats */}
            <div className={`p-4 flex-1 ${isDarkMode ? 'border-t border-gray-700 md:border-t-0 md:border-l' : 'border-t border-gray-200 md:border-t-0 md:border-l'}`}>
              <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-4 gap-4">
                <div>
                  <p className={`mb-1 ${isDarkMode ? 'text-gray-400' : 'text-gray-500'}`}>Market Cap</p>
                  <p className="font-medium">{token.marketCap}</p>
                </div>
                <div>
                  <p className={`mb-1 ${isDarkMode ? 'text-gray-400' : 'text-gray-500'}`}>ETH in Pool</p>
                  <p className="font-medium">{token.ethInPool}</p>
                </div>
                <div>
                  <p className={`mb-1 ${isDarkMode ? 'text-gray-400' : 'text-gray-500'}`}>Migration Threshold</p>
                  <p className="font-medium">{token.migrationThreshold} ETH</p>
                </div>
                <div>
                  <p className={`mb-1 ${isDarkMode ? 'text-gray-400' : 'text-gray-500'}`}>Status</p>
                  <span className={`inline-block px-2 py-1 rounded-full text-xs ${
                    token.migrationStatus === 'Migrated' 
                      ? 'bg-green-100 text-green-800' 
                      : token.migrationStatus === 'Migrating'
                        ? 'bg-blue-100 text-blue-800'
                        : 'bg-yellow-100 text-yellow-800'
                  }`}>
                    {getCountdown(token)}
                  </span>
                </div>
              </div>
            </div>
            
            {/* Right Section - Actions and Socials */}
            <div className={`p-4 flex-shrink-0 flex flex-col justify-between ${isDarkMode ? 'border-t border-gray-700 md:border-t-0 md:border-l' : 'border-t border-gray-200 md:border-t-0 md:border-l'}`}>
              <div className="flex items-center space-x-2 mb-2">
                {token.socials.telegram && (
                  <a 
                    href={token.socials.telegram} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className={`w-8 h-8 rounded-full flex items-center justify-center ${
                      isDarkMode ? 'bg-gray-600 hover:bg-gray-500' : 'bg-gray-200 hover:bg-gray-300'
                    } transition-colors duration-300`}
                    onClick={(e) => e.stopPropagation()}
                  >
                    <FiMessageCircle className="h-4 w-4" />
                  </a>
                )}
                {token.socials.website && (
                  <a 
                    href={token.socials.website} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className={`w-8 h-8 rounded-full flex items-center justify-center ${
                      isDarkMode ? 'bg-gray-600 hover:bg-gray-500' : 'bg-gray-200 hover:bg-gray-300'
                    } transition-colors duration-300`}
                    onClick={(e) => e.stopPropagation()}
                  >
                    <FiExternalLink className="h-4 w-4" />
                  </a>
                )}
                {token.socials.twitter && (
                  <a 
                    href={token.socials.twitter} 
                    target="_blank" 
                    rel="noopener noreferrer"
                    className={`w-8 h-8 rounded-full flex items-center justify-center ${
                      isDarkMode ? 'bg-gray-600 hover:bg-gray-500' : 'bg-gray-200 hover:bg-gray-300'
                    } transition-colors duration-300`}
                    onClick={(e) => e.stopPropagation()}
                  >
                    <FiShare2 className="h-4 w-4" />
                  </a>
                )}
              </div>
              
              <div className="flex items-center space-x-2">
                <button 
                  className={`px-3 py-1 rounded-full flex items-center space-x-1 ${
                    isDarkMode ? 'bg-gradient-to-r from-blue-600 to-purple-600 hover:from-blue-700 hover:to-purple-700 text-white' : 'bg-gradient-to-r from-blue-500 to-purple-500 hover:from-blue-600 hover:to-purple-600 text-white'
                  } transition-all duration-300 ${
                    !token.tradingOpen ? 'opacity-50 cursor-not-allowed' : ''
                  }`}
                  disabled={!token.tradingOpen}
                  onClick={(e) => {
                    e.stopPropagation();
                    navigate(`/swap?token=${token.id}`);
                  }}
                >
                  <svg xmlns="http://www.w3.org/2000/svg" className="h-4 w-4" fill="none" viewBox="0 0 24 24" stroke="currentColor">
                    <path strokeLinecap="round" strokeLinejoin="round" strokeWidth={2} d="M8 7h12m0 0l-4-4m4 4l-4 4m0 6H4m0 0l4 4m-4-4l4-4" />
                  </svg>
                  <span>Swap</span>
                </button>
                <button 
                  onClick={(e) => {
                    e.stopPropagation();
                    toggleLike(token.id);
                  }}
                  className={`flex items-center space-x-1 like-btn ${
                    document.querySelector(`.like-btn[data-token-id="${token.id}"]`)?.classList.contains('liked')
                      ? 'text-red-500'
                      : isDarkMode ? 'text-gray-400 hover:text-red-500' : 'text-gray-500 hover:text-red-500'
                  }`}
                  data-token-id={token.id}
                >
                  <FiHeart className="h-5 w-5" />
                  <span className="text-sm">{likedTokens[token.id] || 0}</span>
                </button>
                <button
                  onClick={(e) => {
                    e.stopPropagation();
                    copyAddress(token.address);
                  }}
                  className={`p-2 rounded-lg ${
                    isDarkMode ? 'bg-gray-600 hover:bg-gray-500' : 'bg-gray-200 hover:bg-gray-300'
                  } transition-colors`}
                >
                  <FiCopy size={16} />
                </button>
              </div>
            </div>
          </div>
          
          {/* Description Section - Hidden by default, expands on click */}
          <div className={`p-4 border-t ${isDarkMode ? 'border-gray-700' : 'border-gray-200'}`}>
            <p className={`mb-2 text-sm ${isDarkMode ? 'text-gray-300' : 'text-gray-700'}`}>
              {expandedDescriptions[token.id] 
                ? token.description 
                : `${token.description.substring(0, 100)}...`}
            </p>
            
            {token.description.length > 100 && (
              <button 
                onClick={(e) => {
                  e.stopPropagation();
                  toggleDescription(token.id);
                }}
                className={`text-sm ${isDarkMode ? 'text-blue-400 hover:text-blue-300' : 'text-blue-600 hover:text-blue-700'}`}
              >
                {expandedDescriptions[token.id] ? 'Show less' : 'Read more...'}
              </button>
            )}
          </div>
        </div>
      ))}
    </div>
  );
};

export default TokenCards; 