      // Generate realistic data
      const generateData = () => {
        const priceData = [];
        const volumeData = [];
        const basePrice = 3400;
        let price = basePrice;
        
        for (let i = 0; i < 100; i++) {
          const time = new Date(Date.now() - (100 - i) * 24 * 60 * 60 * 1000);
          const dateString = time.toISOString().split('T')[0];
          
          const volatility = 0.005;
          const randomChange = (Math.random() - 0.5) * 2 * volatility;
          price = price * (1 + randomChange);
          price = Math.max(3300, Math.min(3500, price));
          
          const open = price;
          const close = price * (1 + (Math.random() - 0.5) * 0.01);
          const high = Math.max(open, close) * (1 + Math.random() * 0.005);
          const low = Math.min(open, close) * (1 - Math.random() * 0.005);
          
          priceData.push({
            time: dateString,
            open: open,
            high: high,
            low: low,
            close: close
          });
          
          const volume = 100 + Math.abs(close - open) / open * 1000;
          volumeData.push({
            time: dateString,
            value: volume
          });
        }
        
        return { priceData, volumeData };
      };

